<template>
  <el-main>
    <div class="wrap-header">
      <div class="item"><span>数据来源：</span><a href="https://dune.com/superfox/0xbitdog-layerzero-airdrop" target="_blank" style="text-decoration:none;color: inherit">dune数据🔗</a></div>
      <div class="item"><span>总地址数：</span>{{ totalAmount }}</div>
      <div class="item"><span>24小时交互：</span>{{ activeAmount }}</div>
      <div class="item"><span>24小时新增：</span>{{ newAmount }}</div>
    </div>
    <el-table ref="multipleTable" :data="addrData" :key="tableKey" border show-summary :summary-method="getSummaries" tooltip-effect="dark" style="width: 100%" @selection-change="selectChange">
      <el-table-column type="selection" width="35"></el-table-column>
      <el-table-column type="index" label="#" :index="indexAdd" width="50"></el-table-column>
      <el-table-column prop="saddr" label="钱包地址" width="110"></el-table-column>
<!--      <el-table-column prop="remark" label="备注" width="90" show-overflow-tooltip></el-table-column>-->
      <el-table-column label="备注" width="90" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-button icon="el-icon-edit" size="mini" circle @click="editOne(scope.$index, scope.row.remark)"></el-button>
          <span>{{ scope.row.remark }}</span>
        </template>
      </el-table-column>
<!--      <el-table-column prop="srecent" label="最新" width="80" label-class-name="label-color" class-name="font-color2" sortable></el-table-column>-->
      <el-table-column label="最新" width="80" label-class-name="label-color" class-name="font-color2" sortable sort-by="srecent">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.srecent" placement="top">
            <span>{{ scope.row.ssrecent }}</span>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="balance" label="余额(e)" width="70"></el-table-column>
      <el-table-column prop="source" label="8条源链" width="150" label-class-name="bg-color1"></el-table-column>
      <el-table-column prop="dest" label="8条目标链" width="150" label-class-name="bg-color1"></el-table-column>
      <el-table-column prop="chain" label="总跨链" width="70" label-class-name="bg-color1"></el-table-column>
      <el-table-column prop="active" label="天/周/月" width="90" label-class-name="bg-color2"></el-table-column>
      <el-table-column prop="screated" label="创建日期" width="100" label-class-name="bg-color2"></el-table-column>
      <el-table-column prop="tx" label="tx" width="50" label-class-name="bg-color3"></el-table-column>
      <el-table-column prop="contract" label="合约" width="50" label-class-name="bg-color3"></el-table-column>
      <el-table-column prop="bridge" label="跨链(u)" width="90" label-class-name="bg-color3"></el-table-column>
  <!--<el-table-column prop="fee" label="gas(e)" width="60"></el-table-column>-->
      <el-table-column prop="airdrop" label="空投:分|排名|前%" sortable  label-class-name="bg-color4"></el-table-column>
      <el-table-column prop="quality" label="品质" width="60"  label-class-name="bg-color4">
        <template slot-scope="scope">
          <span class="quality-color1" v-if="scope.row.quality=='路人'">{{ scope.row.quality }}</span>
          <span class="quality-color2" v-if="scope.row.quality=='反撸'">{{ scope.row.quality }}</span>
          <span class="quality-color3" v-if="scope.row.quality=='保底'">{{ scope.row.quality }}</span>
          <span class="quality-color4" v-if="scope.row.quality=='精品'">{{ scope.row.quality }}</span>
        </template>
      </el-table-column>
      <el-table-column width="50">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteOne(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px">
      <el-pagination background layout="prev, pager, next" :page-size="pageSize" :total="total" hide-on-single-page @current-change="handleChange"></el-pagination>
    </div>
    <div style="margin-top: 20px">
      <el-button type="success" @click="dialog4 = true">查看他人数据</el-button>
      <el-divider direction="vertical"></el-divider>
      <el-button type="primary" icon="el-icon-plus" @click="dialog1 = true">添加地址</el-button>
      <el-button type="primary" icon="el-icon-folder-add" @click="dialog2 = true">批量添加地址</el-button>
      <el-button type="danger" icon="el-icon-delete" @click="deleteAddr">删除选中地址</el-button>
      <el-button type="primary" icon="el-icon-download" @click="downloadData">下载</el-button>
      <el-divider direction="vertical"></el-divider>
      <el-button type="danger" @click="deleteAll">重置页面</el-button>
      <el-button type="success" @click="dialog3 = true">空投规则</el-button>
    </div>
    <el-dialog title="添加地址" :visible.sync="dialog1" width="30%">
      <div>
        <div style="margin-bottom:5px">地址</div><el-input v-model="addr"></el-input>
      </div>
      <div style="margin-top:10px">
        <div style="margin-bottom:5px">备注</div><el-input v-model="remark"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog1 = false">取 消</el-button>
        <el-button type="primary" @click="addAddr">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="批量添加地址" :visible.sync="dialog2" width="30%">
      <el-input type="textarea" :rows="10" placeholder="每行输入1个地址" v-model="addrText"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog2 = false">取 消</el-button>
        <el-button type="primary" @click="addMultiAddr">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="表格数据说明" :visible.sync="dialog3" width="50%">
      <p><strong>数据来源：</strong>数据来源的dune数据，每日更新。</p>
      <p><strong>余额(e)：</strong>余额是8条链的eth余额。</p>
      <p><strong>8条源链：</strong>从源链跨出的次数，8条链为stargate支持的8条链: eth/bnb/ava/pol/arb/op/fan/met，如op跨到arb，源链op+1，目标链arb+1。</p>
      <p><strong>8条目标链：</strong>同上。</p>
      <p><strong>总跨链：</strong>跨出/跨出的链数，超过上述8条链的范围。</p>
      <p><strong>空投分数：</strong>使用dune数据，参照上面的仿arb积分制排名，具体规则如下：</p>
      <ul>
        <li>精品号：排名<10w</li>
        <li>路人号：排名<30w</li>
        <li>保底号：排名<50w</li>
        <li>反撸号：排名>50w</li>
      </ul>
      <ul>
        <li>源链每条1分</li>
        <li>满足2条目标链</li>
        <li>满足5条目标链</li>
        <li>满足10条目标链</li>
        <li>在3个独立月使用过</li>
        <li>在6个独立月使用过</li>
        <li>在9个独立月使用过</li>
        <li>在10个独立周使用过</li>
        <li>在25个独立周使用过</li>
        <li>在50个独立周使用过</li>
        <li>在50个独立日用过</li>
        <li>在100个独立日使用过</li>
        <li>在200个独立日使用过</li>
        <li>钱包使用超过100天</li>
        <li>钱包使用超过250天</li>
        <li>钱包使用超过500天</li>
        <li>有效tx>25次</li>
        <li>有效tx>50次</li>
        <li>有效tx>100次</li>
        <li>有效tx>200次</li>
        <li>与超过10个合约交互过</li>
        <li>与超过25个合约交互过</li>
        <li>与超过50个合约交互过</li>
        <li>与超过100个合约交互过</li>
        <li>使用过跨链桥</li>
        <li>使用过跨链桥跨过$10,000资产</li>
        <li>使用过跨链桥跨过$50,000资产</li>
        <li>使用过跨链桥跨过$250,000资产</li>
        <li>使用过跨链桥跨过$500,000资产</li>
        <li>使用过跨链桥跨过$1000,000资产</li>
      </ul>
    </el-dialog>
    <el-dialog title="查看指定排名数据" :visible.sync="dialog4" width="95%" top="5px" :modal="false" :lock-scroll="false"
       :close-on-click-modal="false" class="dialog-rank">
      <span slot="title" class="dialog-rank-header">
        指定排名:
        <el-input v-model="rank" style="width: 100px;margin-left: 5px;"></el-input>
        <el-button type="success" style="margin-left: 30px;" :loading="loadingRank" @click="queryRank">查  询</el-button>
      </span>
      <el-table ref="miniTable" :data="rankData" :key="table2Key" border style="width: 100%" :show-header="false">
        <el-table-column prop="addr" label="钱包地址" width="200"></el-table-column>
        <el-table-column label="最新" width="80">
          <template slot-scope="scope">
            <el-tooltip :content="scope.row.srecent" placement="top">
              <span>{{ scope.row.ssrecent }}</span>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="balance" label="余额(e)" width="70"></el-table-column>
        <el-table-column prop="source" label="8条源链" width="150"></el-table-column>
        <el-table-column prop="dest" label="8条目标链" width="150"></el-table-column>
        <el-table-column prop="chain" label="总跨链" width="70"></el-table-column>
        <el-table-column prop="active" label="天/周/月" width="90"></el-table-column>
        <el-table-column prop="screated" label="创建日期" width="100"></el-table-column>
        <el-table-column prop="tx" label="tx" width="50"></el-table-column>
        <el-table-column prop="contract" label="合约" width="50"></el-table-column>
        <el-table-column prop="bridge" label="跨链(u)" width="100"></el-table-column>
        <el-table-column prop="airdrop" label="空投:分数/排名"></el-table-column>
      </el-table>
    </el-dialog>
  </el-main>
</template>

<script>
import {getEthBalance as getBalance} from "../../../utils/getblock";
import {formatDate} from "../../../utils/format";
import axios from "axios";
export default {
  name: "Layerzero",
  data() {
    return {
      keyValue: 'lzAddr',
      tableKey: 0,
      table2Key: 0,
      addr: '',
      remark: '',
      addrText: '',
      addrData: [],
      addrSelected: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      dialog4: false,
      rank: 1,
      rankData: [],
      loadingRank: false,
      total: 0,
      pageNum: 1,
      pageSize: 50,
      totalAmount: 2591779,
      activeAmount: 183920,
      newAmount: 10856,
    }
  },
  mounted() {
    // 初始化addr
    let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
    if (addr != undefined) {
      this.total = addr.length;
      addr = addr.slice(0, this.pageSize);
      this.getBasic();
      this.fetchData(addr);
    }
  },
  methods: {
    // 添加地址
    addAddr() {
      this.dialog1 = false;
      let patter = /^0x[0-9a-fA-F]{40}$/;
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      if (addr == null) addr = [];
      let addr2 = addr.map((item, index, arr) => {
        return item.addr;
      });
      if (this.addr.match(patter) != undefined && addr2.indexOf(this.addr) == -1) {
        addr.push({'addr': this.addr, 'remark': this.remark});
        window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
        this.fetchData([{'addr': this.addr, 'remark': this.remark}]);
      } else {
        this.$message({
          message: '钱包地址格式不对或重复',
          type: 'warning'
        });
      }
    },
    // 添加多个地址
    addMultiAddr() {
      this.dialog2 = false;
      let arr = this.addrText.split("\n");
      let patter = /^0x[0-9a-fA-F]{40}$/;
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      if (addr == null) addr = [];
      let addr2 = addr.map((item, index, arr) => {
        return item.addr;
      });
      let newAddr =[];
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].match(patter) != undefined && addr2.indexOf(arr[i]) == -1) {
          newAddr.push({'addr': arr[i], 'remark': ''});
          addr.push({'addr': arr[i], 'remark': ''});
        } else {
          this.$message({
            message: '钱包地址格式不对或重复',
            type: 'warning'
          });
        }
      }
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
      if (newAddr.length > this.pageSize) {
        this.$alert('添加地址数>' + this.pageSize + '，刷新页面加载', '提示', {
          confirmButtonText: '确定',
          callback: () => {
            window.location.reload();
          }
        });
      } else {
        this.fetchData(newAddr);
      }
    },
    // 编辑一行
    editOne(index, text) {
      this.$prompt("备注", {
        confirmButtonText: "确定",
        inputValue: text
      }).then(({ value }) => {
        this.addrData[index]["remark"] = value;
        this.tableKey = Math.random();
        let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
        let index2 = index + (this.pageNum - 1) * this.pageSize;
        addr[index2]["remark"] = value;
        window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
      });
    },
    // 删除一行
    deleteOne(index) {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      this.addrData.splice(index, 1);
      addr.splice(index, 1);
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
    },
    // 删除地址
    deleteAddr() {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      for (let i = 0; i < addr.length; i++) {
        if(this.addrSelected.indexOf(addr[i].addr) != -1) {
          this.addrData.splice(i, 1);
          addr.splice(i, 1);
          i--;
        }
      }
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
    },
    // 全部删除
    deleteAll() {
      this.addrData = [];
      window.localStorage.setItem(this.keyValue, JSON.stringify([]));
    },
    // 下载数据
    downloadData() {

    },
    // 选择变化
    selectChange(val) {
      this.addrSelected = [];
      for (let i = 0; i < val.length; i++) {
        this.addrSelected.push(val[i].addr);
      }
    },
    // index索引
    indexAdd(index) {
      return index + 1 + (this.pageNum - 1) * this.pageSize;
    },
    // 页面变化
    handleChange(val) {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      let min = (val - 1) * this.pageSize;
      this.pageNum = val;
      addr = addr.slice(min, min + this.pageSize);
      this.addrData = [];
      this.addrSelected = [];
      this.fetchData(addr);
    },
    // 获取数据
    fetchData(arr) {
      let len = this.addrData.length;
      let addrArr = [];
      for (let i = 0; i < arr.length; i++) {
        let addr = arr[i].addr;
        addrArr.push(addr);
        this.addrData[len+i] = [];
        this.addrData[len+i]['addr'] = addr;
        this.addrData[len+i]['saddr'] = addr.substring(0,6) + "..." + addr.substring(addr.length-4, addr.length);
        this.addrData[len+i]['remark'] = arr[i].remark;
        let net = ["ethereum", "bsc", "avax", "arbitrum", "optimism", "ftm", "metis"]; //
        for (let j = 0; j < net.length; j++) {
          this.addrData[len+i]['balance'] = 0;
          getBalance(net[j], addr).then(res => {
            this.addrData[len+i]['balance'] = Number(this.addrData[len+i]['balance']);
            this.addrData[len+i]['balance'] += Number(res)? Number(res) : 0;
            this.addrData[len+i]['balance'] = this.addrData[len+i]['balance'].toFixed(4);
            this.tableKey = Math.random();
          });
        }
      }
      axios.get("/api/query", {params: {net: "layerzero", addr: addrArr.toString().toLowerCase()}}).then(res => {
        const addrRet = res.data;
        if (addrRet == "ERROR")
          return;
        for (let i = 0; i < addrRet.length; i++) {
          for (let j = len; j < this.addrData.length; j++) {
            if (this.addrData[j]["addr"].toLowerCase() == addrRet[i]["addr"]) {
              this.addrData[j] = {
                ...addrRet[i],
                ...this.addrData[j]
              };
            }
          }
        }
        for (let k = 0; k < this.addrData.length; k++) {
          this.addrData[k]["srecent"] = this.addrData[k]["recent"]? formatDate(this.addrData[k]["recent"], "M/D"):"";
          this.addrData[k]["ssrecent"] = this.addrData[k]["recent"]? formatDate(this.addrData[k]["recent"], "AGO"):"";
          this.addrData[k]["screated"] = this.addrData[k]["created"]?formatDate(this.addrData[k]["created"], "Y/M/D"):"";
          this.addrData[k]["airdrop"] = this.addrData[k]["score"]?(this.addrData[k]["score"] + "/" + this.addrData[k]["urank"] + "/" + Math.ceil(this.addrData[k]["urank"] / 21700) + "%"):"";
          this.addrData[k]["quality"] = "反撸";
          if (this.addrData[k]["urank"] <= 10 * 10000) {
            this.addrData[k]["quality"] = "精品";
          } else if (this.addrData[k]["urank"] <= 30 * 10000) {
            this.addrData[k]["quality"] = "路人";
          } else if (this.addrData[k]["urank"] <= 50 * 10000) {
            this.addrData[k]["quality"] = "保底";
          }
        }
        this.tableKey = Math.random();
      })
    },
    // 获取指定排名
    queryRank() {
      this.loadingRank = true;
      axios.get("/api/queryrank", {params: {rank: this.rank}}).then(res => {
        this.loadingRank = false;
        const addrRet = res.data;
        if (addrRet == "ERROR")
          return;
        this.rankData = addrRet;
        this.rankData[0]["srecent"] = this.rankData[0]["recent"]? formatDate(this.rankData[0]["recent"], "M/D"):"";
        this.rankData[0]["ssrecent"] = this.rankData[0]["recent"]? formatDate(this.rankData[0]["recent"], "AGO"):"";
        this.rankData[0]["screated"] = this.rankData[0]["created"]?formatDate(this.rankData[0]["created"], "Y/M/D"):"";
        this.rankData[0]["airdrop"] = this.rankData[0]["score"]?(this.rankData[0]["score"] + "/" + this.rankData[0]["urank"] + "/" + Math.ceil(this.rankData[0]["urank"]*100/this.totalAmount) + "%"):"";
        this.rankData[0]["quality"] = "反撸";
        if (this.rankData[0]["urank"] <= 10 * 10000) {
          this.rankData[0]["quality"] = "精品";
        } else if (this.rankData[0]["urank"] <= 30 * 10000) {
          this.rankData[0]["quality"] = "路人";
        } else if (this.rankData[0]["urank"] <= 50 * 10000) {
          this.rankData[0]["quality"] = "保底";
        }

        // 查询余额
        let net = ["ethereum", "bsc", "avax", "arbitrum", "optimism", "ftm", "metis"]; //
        for (let j = 0; j < net.length; j++) {
          this.rankData[0]['balance'] = 0;
          getBalance(net[j], this.rankData[0]["addr"]).then(res => {
            this.rankData[0]['balance'] = Number(this.rankData[0]['balance']);
            this.rankData[0]['balance'] += Number(res)? Number(res) : 0;
            this.rankData[0]['balance'] = this.rankData[0]['balance'].toFixed(4);
            this.table2Key = Math.random();
          });
        }
      })
    },
    // 表尾
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = '合计';
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value)) && ([5]).indexOf(index) >= 0) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0).toFixed(4);
        } else {
          sums[index] = '';
        }
      });
      return sums;
    },
    // 获取基础信息
    getBasic() {
      axios.get("/api/basic").then(res => {
        const ret = res.data;
        for (let i = 0; i < ret.length; i++) {
          if (ret[i]["name"] == "totalUser") {
            this.totalAmount = ret[i]["value"];
          } else if (ret[i]["name"] == "activeUser") {
            this.activeAmount = ret[i]["value"];
          } else if (ret[i]["name"] == "newUser") {
            this.newAmount = ret[i]["value"];
          }
        }
      })
    }
  }
}
</script>

