    span
      color $textColor

/deep/.el-table
  .cell
    text-align center
    padding 0 5px
  .el-table__cell
    padding 8px 0
  .bg-color1
    color #fff
    background-color $color1 !important
  .bg-color2
    color #fff
    background-color $color2 !important
  .bg-color3
    color #fff
    background-color $color3 !important
  .bg-color4
    color #fff
    background-color $color4 !important
  .font-color2
    color $color2
    font-weight bold
  .label-color
    color #909399

/deep/.dialog-rank .el-dialog__body
    padding-top 5px

// quality color
.quality-color1
  color #fff
  background-color $color1
  padding 3px 8px
  border-radius 4px
  font-weight bold
